import React, { Component } from "react";
import { GridList, GridTile, GridListTile } from "material-ui/GridList";
import IconButton from "material-ui/IconButton";
import ZoomIn from "material-ui/svg-icons/action/zoom-in";
import { Card } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import { withStyles } from "@material-ui/core/styles";
import code from "../code.jpg";

const styles = theme => ({
  root: {
    backgroundColor: theme.palette.background.paper,
    color: "rgba(25, 25, 25, 0.54)",
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "space-around",
    overflow: "hidden"
  },
  gridList: {
    width: 400,
    height: 400
  },
  title: {
    color: theme.palette.primary.dark
  },
  titleBar: {
    background:
      "linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)"
  }
});

class ImageResult extends Component {
  state = {
    open: false,
    currentRepo: ""
  };

  handleOpen = img => {
    this.setState({
      open: true,
      currentRepo: img
    });
  };

  handleClose = () => {
    this.setState({
      open: false
    });
  };

  render() {
    let repoListContent;
    const { classes } = this.props;
    const { repository } = this.props;
    if (repository) {
      repoListContent = (
        <GridList cols={2}>
          {repository.map(repo => (
            <GridTile
              title={repo.name}
              key={repo.id}
              subtitle={<span>{repo.description}</span>}
              actionIcon={
                <IconButton onClick={() => this.handleOpen(repo.full_name)}>
                  <ZoomIn color="white"></ZoomIn>
                </IconButton>
              }
            >
              <img src={code} alt="code" />
            </GridTile>
          ))}
        </GridList>
      );
    } else {
      repoListContent = null;
    }

    return <Card>{repoListContent}</Card>;
  }
}

export default withStyles(styles, { withTheme: true })(ImageResult);
